<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
       
        <el-form-item label="工站">
          <el-select v-model="listQuery.work_id" placeholder="请选择">
           <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialogForm'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>不良原因列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增不良原因弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
            <!-- multiple -->

      <el-form-item label="工站" prop="work_id">
          <el-select
            v-model="dialogForm.work_id"
            placeholder="请选择"
            collapse-tags
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' search'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="不良现象" prop="unqualified_result_name">
          <el-input v-model="dialogForm.unqualified_result_name"></el-input>
        </el-form-item>

      
        <el-form-item label="不良原因" prop="content">
           <el-input type="textarea" :rows="3" v-model="dialogForm.content"></el-input>
        </el-form-item>
         <el-form-item label="处理方式" prop="process_mode">
           <el-input type="textarea" :rows="3" v-model="dialogForm.process_mode"></el-input>
        </el-form-item>
   
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "UserList",
  //  props: {
  //   tableCol: {
  //     type: Array,
  //     default: () => [],
  //   },
  // },
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        unqualified_result_code: "",
        work_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        unqualified_result_code: "",
        work_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增不良原因弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        unqualified_result_code: "",
        work_id: "",
        unqualified_result_name : "",
        content: "",
        process_mode: "",
        operate_user: "",
        time: "",
        work_idArr: [],
      },
      dialogFormBase: {
        unqualified_result_code: "",
        work_id: "",
        unqualified_result_name : "",
        content: "",
        process_mode: "",
        operate_user: "",
        time: "",
        work_idArr: [],
      },
      dialogFormRules: {
        work_id: [{ required: true, message: "请选择工站", trigger: "change" }],
        unqualified_result_name: [{ required: true, message: "请输入不良现象", trigger: "blur" }],
      },
      // 表格列
      is_db_common:true,
      tableCol: [
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "不良编码",
          field: "unqualified_result_code",
          align: "center",
        },
        {
          label: "不良原因",
          field: "unqualified_result_name",
          align: "center",
        },
        {
          label: "备注",
          field: "content",
          align: "center",
        },
        {
          label: "处理方式",
          field: "process_mode",
          align: "center",
        },
        {
          label: "操作人员",
          field: "operate_user",
          align: "center",
        },
        {
          label: "创建时间",
          field: "time",
          align: "center",
        },
   
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState(["CurrentUserInfo","ProductIdNameList", "WorkCodeNameList"]),
  },
  methods: {
    ...mapActions(["getLogisticGetProductId", "getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticWorkUnqualifiedResult(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticWorkUnqualifiedResult(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticWorkUnqualifiedResult(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticWorkUnqualifiedResult(this.listQuery);
    },
    // 不良原因列表 查询
    async getLogisticWorkUnqualifiedResult(params) {
      // 工站内码和工站名称接口： 查询
      await this.getLogisticWorkCodeName();

      baseManagement.getLogisticWorkUnqualifiedResult(params).then((res) => {
        console.log("不良原因列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
           this.WorkCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.work_id == CodeNameListItem.work_id) {
                  tableDataItem.work_name = CodeNameListItem.work_id+"_"+CodeNameListItem.work_name;
                }
              });

          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 不良原因列表 新增
    postLogisticWorkUnqualifiedResult(params) {
      baseManagement.postLogisticWorkUnqualifiedResult(params).then((res) => {
        console.log("不良原因列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticWorkUnqualifiedResult(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 不良原因列表 新增
    addBtn() {
      this.dialogTitle = "新增不良原因";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },
    roleChane(value){
      console.log(value);
      if(value=="超级管理员"){
        this.dialogForm.user_product_id=""
      }
    },

    // 不良原因列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改不良原因";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = {
        unqualified_result_code: rowData.unqualified_result_code,
        work_id: rowData.work_id,
        unqualified_result_name: rowData.unqualified_result_name,
        content: rowData.content,
        process_mode: rowData.process_mode,
        operate_user: rowData.operate_user,
        time: rowData.time,
      };
    },

    // 不良原因列表 修改
    getLogisticPutWorkUnqualifiedResult(params) {
      baseManagement.getLogisticPutWorkUnqualifiedResult(params).then((res) => {
        console.log("不良原因列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticWorkUnqualifiedResult(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      console.log(this.dialogForm);
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口

            let params =JSON.parse(JSON.stringify(this.dialogForm));
        

          if (this.dialogTitle == "新增不良原因") {
            console.log("新增不良原因", params);
            this.postLogisticWorkUnqualifiedResult(params);
          } else if (this.dialogTitle == "修改不良原因") {
            console.log("修改不良原因", params);
            this.getLogisticPutWorkUnqualifiedResult(params);
          }
        }
      });
    },

    // 不良原因列表 删除
    getLogisticDeleteWorkUnqualifiedResult(params) {
      baseManagement.getLogisticDeleteWorkUnqualifiedResult(params).then((res) => {
        console.log("不良原因列表 删除", res);
        if (res.code == 0) {
          this.getLogisticWorkUnqualifiedResult(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeleteWorkUnqualifiedResult({ unqualified_result_code: rowData.unqualified_result_code });
      });
    },
  },
  mounted() {
    console.log("this.CurrentUserInfo 213",this.CurrentUserInfo);
  
    // 产品名称和编码接口
    this.getLogisticGetProductId();
    // 不良原因列表 查询
    this.getLogisticWorkUnqualifiedResult(this.listQuery);
  },
};
</script>

